#root {
  height: 100%;
}
.App {
  clear: both;
  overflow: hidden;
  height: 100%;
  padding-right: 400px;
}
.App-left {
  float: left;
  width: 100%;
  height: 100%;
  text-align: center;
}
.App-right {
  float: right;
  position: relative;
  right: -400px;
  width: 400px;
  height: 100%;
  margin-left: -400px;
  padding: 20px;
  background-color: #fff;
}

.App-right .App-right-bottom {
  margin-top: 50px;
}



