.OutsiderFrame h2 {
  padding: 10px 0 8px;
  font-size: 18px;
  color: #2d61c5;
  text-align: center;
  letter-spacing: 1px;
  border: 3px solid #5b89e1;
  border-radius: 5px;
}

.OutsiderFrame .SendForm .position {
  margin-top: 40px;
  text-align: center;
}
.OutsiderFrame .SendForm .position span {
  display: inline-block;
  width: 62px;
  padding: 10px 0;
  font-size: 42px;
  color: #5b89e1;
  vertical-align: middle;
}
.OutsiderFrame .SendForm .position span.f,
.OutsiderFrame .SendForm .position span.l {
  color: #fff;
  background-color: #5b89e1;
  border-radius: 4px;
}
.OutsiderFrame .SendForm .position span.c {
  width: 30px;
  margin-top: -10px;
}
.OutsiderFrame .SendForm .time,
.OutsiderFrame .SendForm .place {
  clear: left;
  overflow: hidden;
  position: relative;
  margin-top: 20px;
  padding: 5px 10px 0 70px;
  font-size: 15px;
  font-weight: bold;
}
.OutsiderFrame .SendForm .time {
  padding-bottom: 18px;
}
.OutsiderFrame .poa {
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -6px;
}
.OutsiderFrame .SendForm .place button {
  float: left;
  width: 31.3%;
  margin-left: 3%;
  padding: 3px 0;
  font-size: 14px;
  color: #2d61c5;
  text-align: center;
  border: 2px solid #5b89e1;
  border-radius: 2px;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.OutsiderFrame .SendForm .place button.select {
  font-weight: bold;
  color: #fff;
  background-color: #5b89e1;
}
.OutsiderFrame .SendForm .place .poa + button {
  margin-left: 0;
}
.OutsiderFrame .SendFormButton {
  display: block;
  width: 100%;
  margin-top: 40px;
  padding: 8px 0;
  font-size: 15px;
  font-weight: bold;
  text-align: center;
  color: #fff;
  background-color: #5b89e1;
  border-radius: 3px;
}
.OutsiderFrame .SendFormButton:hover {
  text-decoration: underline;
}

.OutsiderFrame .EmptList > div {
  position: fixed;
  width: 0;
  height: 100%;
  text-align: center;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.OutsiderFrame .EmptList > div.right {
  right: 0;
  top: 0;
  width: 0;
  height: 100%;
  background-color: #fbc116;
}
.OutsiderFrame .EmptList > div.top {
  left: 0;
  top: 0;
  width: 100%;
  height: 0;
  background-color: #86b799;
}
.OutsiderFrame .EmptList > div.bottom {
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0;
  background-color: #e70a07;
}
.OutsiderFrame .EmptList.act > div.right {
  width: 100%;
}
.OutsiderFrame .EmptList.act > div.top {
  height: 100%;
  -webkit-transition-delay: 0.7s;
       -o-transition-delay: 0.7s;
          transition-delay: 0.7s;
}
.OutsiderFrame .EmptList.act > div.bottom {
  height: 100%;
  -webkit-transition-delay: 1.4s;
       -o-transition-delay: 1.4s;
          transition-delay: 1.4s;
}

.OutsiderFrame .EmptList > div.bottom .EmptInfo {
  position: absolute;
  top: 50%;
  width: 100%;
  margin-top: 0;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
  opacity: 0;
}
.OutsiderFrame .EmptList.act > div.bottom .EmptInfo {
  margin-top: -200px;
}
.OutsiderFrame .EmptList > div.bottom .EmptInfo .p {
  font-size: 20px;
  color: #fff;
}
.OutsiderFrame .EmptList > div.bottom .EmptInfo .p span {
  font-weight: bold;
  color: yellow;
}
.OutsiderFrame .EmptList > div.bottom .EmptInfo .t {
  font-family: "Dosis", sans-serif;
  font-size: 240px;
  font-weight: 700;
  color: #fff;
}
.OutsiderFrame .EmptList > div.bottom .EmptInfo .s {
  margin-top: 30px;
  font-size: 20px;
  color: #fff;
}
.OutsiderFrame .EmptList > div.bottom .EmptInfo .s span {
  font-weight: bold;
  color: yellow;
}
.OutsiderFrame .EmptList > div.bottom .EmptInfo .b button {
  margin-top: 60px;
  padding: 8px 35px;
  font-size: 15px;
  font-weight: bold;
  background-color: #fff;
  border-radius: 5px;
  opacity: 0.9;
}
.OutsiderFrame .EmptList.act > div.bottom .EmptInfo {
  opacity: 1;
  -webkit-transition-delay: 2.1s;
       -o-transition-delay: 2.1s;
          transition-delay: 2.1s;
}



