.TimeForm {
  position: relative;
  height: 100%;
  background-color: #0862e0;
  -webkit-transition: all 20s ease;
  -o-transition: all 20s ease;
  transition: all 20s ease;
}
.TimeForm.skyblue {
  background-color: #53ccfb;
}
.TimeForm.green {
  background-color: #28d75c;
}
.TimeForm.red {
  background-color: #e70a07;
}
.TimeForm.blue {
  background-color: #0862e0;
}
.TimeForm.orange {
  background-color: #ae2b4d;
}
.TimeForm.gray {
  background-color: #595959;
}

.TimeForm .TimeInfo {
  position: absolute;
  top: 50%;
  width: 100%;
  margin-top: -164px;
  font-family: "Dosis", sans-serif;
  font-size: 240px;
  font-weight: 700;
  color: #fff;
  text-align: center;
  letter-spacing: 20px;
}
.TimeForm .TimeInfo .am-pm {
  display: inline-block;
  margin: 18px 20px 0 0;
  font-size: 80px;
  font-weight: 600;
  letter-spacing: 0;
}
.TimeForm .TimeInfo .call-loan {
  display: inline-block;
  margin: -130px 10px 0;
  vertical-align: middle;
}

.TimeForm .DateInfo .day {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding: 10px 0;
  font-size: 20px;
  font-weight: bold;
  color: #fff;
  text-align: center;
  background-color: #000;
  opacity: 0.4;
}
.TimeForm .DateInfo .date {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  margin-top: 108px;
  font-size: 32px;
  font-weight: bold;
  color: #fff;
  text-align: center;
}



