.SendForm h2 {
  padding: 10px 0 8px;
  font-size: 18px;
  text-align: center;
  letter-spacing: 1px;
  border: 1px solid #ddd;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 4px #ddd;
          box-shadow: 0 0 4px #ddd;
}

.SendForm .todo_form {
  position: relative;
  margin-top: 15px;
}
.SendForm .todo_form input {
  width: 100%;
  height: 40px;
  padding: 0 70px 0 10px;
  font-size: 15px;
  font-weight: bold;
  border: 3px solid #5580cf;
}
.SendForm .todo_form button {
  position: absolute;
  right: 0;
  top: 0;
  width: 60px;
  height: 40px;
  font-weight: bold;
  color: #d9fbfa;
  text-align: center;
  background-color: #5580cf;
}

.TodoFrame .InfoList {
  margin-top: 10px;
  border: 2px solid #ccc;
  border-radius: 4px;
}
.TodoFrame .InfoList li {
  position: relative;
  padding: 5px 50px 5px 5px;
  border-top: 1px solid #ddd;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.TodoFrame .InfoList li.tac {
  padding: 10px 0;
  font-size: 14px;
  color: #777;
  text-align: center;
  line-height: 18px;
}
#root .TodoFrame .InfoList li:first-child {
  border-top: none;
}
.TodoFrame .InfoList li a {
  display: inline-block;
  padding: 5px;
  font-size: 14px;
  line-height: 18px;
  cursor: pointer;
}
.TodoFrame .InfoList li a.acted {
  color: #999;
  text-decoration: line-through;
}
.TodoFrame .InfoList li button {
  position: absolute;
  right: 10px;
  top: 50%;
  margin-top: -14px;
  font-size: 18px;
  font-weight: bold;
  color: #555;
  line-height: 24px;
}
.TodoFrame .InfoList.gray {
  border: 2px solid #ccc;
}
.TodoFrame .InfoList.gray li {
  color: #444;
  border-top: 1px solid #f7f7f7;
  background-color: #ddd;
}
.TodoFrame .InfoList.gold {
  border-top: 1px solid #f7f7f7;
  border: 2px solid #9a8308;
}
.TodoFrame .InfoList.gold li {
  color: #444;
  border-top: 1px solid #fff5be;
  background-color: gold;
}
.TodoFrame .InfoList.blue {
  border: 2px solid #000173;
}
.TodoFrame .InfoList.blue li {
  color: #fff;
  border-top: 1px solid #9899ff;
  background-color: #3333ff;
}
.TodoFrame .InfoList.blue li a.acted {
  color: #bebfff;
}
.TodoFrame .InfoList.red {
  border: 2px solid #750200;
}
.TodoFrame .InfoList.red li {
  color: #fff;
  border-top: 1px solid #ff7c79;
  background-color: red;
}
.TodoFrame .InfoList.red li a.acted {
  color: #ffb4b2;
}

.ColorSet {
  margin-top: 10px;
  text-align: center;
}
.ColorSet > button {
  display: inline-block;
  width: 35px;
  height: 35px;
  margin: 0 5px;
  vertical-align: middle;
  border: 0 none;
  background: transparent;
  cursor: pointer;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.ColorSet > button.default {
  border: 1px solid #ccc;
}
.ColorSet > button.default:hover {
  border: 1px solid #aaa;
}
.ColorSet > button.gray {
  background-color: gray;
}
.ColorSet > button.gray:hover {
  background-color: #616161;
}
.ColorSet > button.gold {
  background-color: gold;
}
.ColorSet > button.gold:hover {
  background-color: #e6c200;
}
.ColorSet > button.blue {
  background-color: blue;
}
.ColorSet > button.blue:hover {
  background-color: #0101e2;
}
.ColorSet > button.red {
  background-color: red;
}
.ColorSet > button.red:hover {
  background-color: #e20000;
}



